import { api } from "api";
import { GET_USER, AUTH_ERROR } from "./types";

export const getUser = (formProps = null, callback) => async (dispatch) => {
  const token = localStorage.getItem("access_token");
  try {
    const response = await api.get("/users/mail2bishnoi", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    console.log(response.data);
    dispatch({ type: GET_USER, payload: null });
    callback();
  } catch (e) {
    dispatch({ type: AUTH_ERROR, payload: "Signup Error" });
  }
};
