import React, { Component } from "react";
import { reduxForm, Field, change } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";
import * as actions from "actions";

class ActivateAccount extends Component {
  componentDidMount() {
    if (this.props.match.params.username) {
      this.props.dispatch(
        change("accountActivate", "userName", this.props.match.params.username)
      );
    }
    if (this.props.match.params.activationcode) {
      this.props.dispatch(
        change(
          "accountActivate",
          "activationcode",
          this.props.match.params.activationcode
        )
      );
    }
  }
  onSubmit = (formValues) => {
    this.props.activateAccount(formValues, (response) => {
      console.log("inside activate account callback");
      this.props.history.push("/signin");
    });
  };
  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="_form_text red">{error}</div>;
    }
  }
  renderInput = ({ input, label, type, placeholder, meta }) => {
    const className = `_form_group ${
      meta.error && meta.touched ? "_error" : ""
    }`;
    return (
      <div className={className}>
        <label htmlFor={input.name}>{label}</label>
        <input
          {...input}
          autoComplete="off"
          type={type}
          placeholder={placeholder}
          className="_form_control"
        />
        {this.renderError(meta)}
      </div>
    );
  };
  render() {
    const { handleSubmit } = this.props;
    return (
      <div>
        <section className="section-signin">
          <div className="signin">
            <h4 className="signin__heading">Please Activate Your Account</h4>
            <div className="signin__box">
              <form
                onSubmit={handleSubmit(this.onSubmit)}
                className="form"
                noValidate
              >
                <Field
                  name="userName"
                  type="text"
                  component={this.renderInput}
                  label="Username:"
                  placeholder="Username"
                />
                <Field
                  name="activationCode"
                  type="text"
                  component={this.renderInput}
                  label="Activation Code:"
                  placeholder="Activation Code"
                />
                <button type="submit">Activate your account</button>
              </form>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
const validate = (formValues) => {
  const errors = {};
  if (!formValues.userName) {
    errors.userName = "You must enter an username";
  }

  if (!formValues.activationCode) {
    errors.activationCode = "You must enter a verification code";
  }
  return errors;
};

function mapStateToProps(state) {
  return {
    errorMessage: state.auth.errorMessage,
    auth: "",
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "accountActivate", validate })
)(ActivateAccount);
