import React, { Component } from "react";
import { Link } from "react-router-dom";

import logo from "assets/img/sprite.svg";
import avtar from "assets/img/user.jpg";

class AppHeader extends Component {
  state = { anchorEl: null };
  onClickHandle = event => {
    this.setState({ anchorEl: event.currentTarget });
  };
  onHandleClose = () => {
    this.setState({ anchorEl: null });
  };
  render() {
    return (
      <header className="header">
        <nav className="left-nav">
          <div className="left-nav__icon-box">
            <svg className="logo">
              <use xlinkHref={logo + "#icon-loop2"}></use>
            </svg>
          </div>
          <div className="left-nav__icon-box">
            <Link to="/dashboard/buy" className="left-nav_link">
              Buy
            </Link>
          </div>
          <div className="left-nav__icon-box">
            <Link to="/dashboard/sell" className="left-nav_link">
              Sell
            </Link>
          </div>
        </nav>
        <nav className="user-nav">
          <div className="user-nav__icon-box">
            <Link to="/dashboard/deposite" className="user-nav_link">
              Deposite
            </Link>
          </div>
          <div className="user-nav__icon-box">
            <Link to="/dashboard/withdraw" className="user-nav_link">
              Withdraw
            </Link>
          </div>
          <div className="user-nav__icon-box">
            <svg className="user-nav__icon">
              <use xlinkHref={logo + "#icon-chat"}></use>
            </svg>
            <span className="user-nav__notifications">13</span>
          </div>
          <div className="user-nav__user" onClick={this.onClickHandle}>
            <img
              src={avtar}
              className="user-nav__user-photo"
              alt="user avtar"
            />
            <span className="user-nav__user-name">Ashutosh</span>
          </div>
        </nav>
      </header>
    );
  }
}
export default AppHeader;
