import React, { Component } from "react";
import { Link } from "react-router-dom";
import { reduxForm, Field } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";
import Loading from "components/progressbar/Loading";

import * as actions from "actions";

class Signin extends Component {
  componentDidMount() {
    if (this.props.auth) {
      this.props.history.push("/dashboard");
    }
  }
  onSubmit = (formValues) => {
    this.props.signin(formValues, (result) => {
      if (result.status === 200) {
        this.props.history.push("/dashboard");
      }
    });
  };
  renderError({ error, touched }) {
    if (touched && error) {
      return <div className="_form_text red">{error}</div>;
    }
  }
  renderInput = ({ input, label, type, placeholder, meta }) => {
    const className = `_form_group ${
      meta.error && meta.touched ? "_error" : ""
    }`;
    return (
      <div className={className}>
        <label htmlFor={input.name}>{label}</label>
        <input
          {...input}
          autoComplete="off"
          type={type}
          placeholder={placeholder}
          className="_form_control"
        />
        {this.renderError(meta)}
      </div>
    );
  };
  render() {
    const { handleSubmit, pristine, submitting } = this.props;
    return (
      <section className="section-signin">
        <div className="signin">
          <h2 className="signin__heading">ExchangerPoint</h2>
          <nav>
            <ul className="signin__navigation">
              <li className="signin__item">
                <Link to="#" className="signin__link signin__link--active">
                  Login
                </Link>
              </li>
              <li className="signin__item">
                <Link to="/signup" className="signin__link">
                  Signup
                </Link>
              </li>
            </ul>
          </nav>
          <div className="signin__box">
            <form
              onSubmit={handleSubmit(this.onSubmit)}
              className="form"
              noValidate
            >
              <Field
                name="username"
                type="text"
                component={this.renderInput}
                label="Username:"
                placeholder="Username"
              />
              <Field
                name="password"
                type="password"
                component={this.renderInput}
                label="Password:"
                placeholder="Password"
              />
              <button type="submit" disabled={pristine || submitting}>
                {this.props.loading ? <Loading /> : "Login to your account"}
              </button>
            </form>
          </div>
          <Link to="#" className="forgot-pasword">
            Forgot Your Password?
          </Link>
        </div>
      </section>
    );
  }
}
const validate = (formValues) => {
  const errors = {};
  if (!formValues.username) {
    errors.username = "You must enter an username";
  }

  if (!formValues.password) {
    errors.password = "You must enter a password";
  }
  return errors;
};

function mapStateToProps(state) {
  return {
    loading: state.toggleLoading.toggleLoading,
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "signin", validate })
)(Signin);
