import { AUTH_USER, AUTH_ERROR, LOG_OUT } from "actions/types";
const INITIAL_STATE = {
  authenticated: localStorage.getItem("access_token") ? true : false,
  errorMessage: "",
  user: null,
};

export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    case AUTH_USER:
      return { ...state, user: action.payload, authenticated: true };
    case LOG_OUT:
      return { ...state, user: action.payload, authenticated: false };
    case AUTH_ERROR:
      return { ...state, errorMessage: action.payload };
    default:
      return state;
  }
}
