import { api, signinapi } from "api";
import { AUTH_USER, AUTH_ERROR, LOG_OUT, TOGGLE_LOADING } from "./types";
import { toast } from "react-toastify";

export const signin = (formProps, callback) => async (dispatch) => {
  dispatch({ type: TOGGLE_LOADING, payload: true });
  const data = JSON.stringify(formProps);
  toast.info("Authentication is in progress");
  try {
    const response = await signinapi.post("", data, {
      params: {
        grant_type: "password",
        scope: "write",
        username: formProps.username,
        password: formProps.password,
      },
    });
    if (response.status === 200) {
      toast.success("Authentication Successful");
      localStorage.setItem("access_token", response.data.access_token);
      dispatch({ type: AUTH_USER, payload: response.data });
      dispatch({ type: TOGGLE_LOADING, payload: false });
      callback(response);
    }
  } catch (e) {
    toast.error("Authentication Failed");
    dispatch({ type: TOGGLE_LOADING, payload: false });
  }
};
export const signout = () => async (dispatch) => {
  localStorage.removeItem("access_token");
  dispatch({ type: LOG_OUT, payload: null });
  return {
    type: AUTH_USER,
    payload: "",
  };
};
export const signup = (formProps, callback) => async (dispatch) => {
  const data = JSON.stringify(formProps);
  try {
    const response = await api.post("/users/create/", data);
    callback(response);
  } catch (e) {
    dispatch({ type: AUTH_ERROR, payload: "Signup Error" });
  }
};
export const activateAccount = (formProps, callback) => async (dispatch) => {
  const data = JSON.stringify(formProps);
  try {
    const response = await api.put("/users/activate/", data);
    if (response.status === 200) {
    }
    callback();
  } catch (e) {}
};
